class GUIWindow {
	constructor(x, y, w, h, margin,
				title, message) {
		this.x = x;
		this.y = y;
		this.w = w;
		this.h = h;
		this.margin = margin;
		this.title = title;
		this.message = message;
	}

	draw(opacity) {
		push();

		// stroke(0, opacity);
		// fill(0, opacity);
		// rect(this.x, this.y, this.w, this.h);
		tint(255, opacity);
		image(gfx.questBox, this.x, this.y);

		fill(255, opacity);
		textSize(32);
		textFont(pixelFont);
		textAlign(CENTER);
		text(this.title, this.x + this.w / 2, this.y + 4 * this.margin);

		// fill(255, opacity);
		// rect(this.x, this.y + 5 * this.margin, this.w, this.h - 5 * this.margin);

		stroke(0, opacity);
		textSize(30);
		text(this.message, this.x + this.w / 2, this.y + this.h / 3 + 2 * this.margin);

		pop();
	}
}